import React from 'react';
import { ArticleTitle, ArticleDescription, Footer, Paragraph, Anchor, Indicator } from '../StyledComponents';
import NewsAPI from 'newsapi';
import { useEffect } from 'react';
import { createRef } from 'react';
import LaunchIcon from '@material-ui/icons/Launch';
import { useNewsContext } from '../context';
import Loader from './Loader';
import NoResults from './NoResults';
import _ from 'lodash';


const Article = ({ categories }) => {

    const { setFetchError, isLoading, setLoading, topHeadlines, currentArticleIndex, setCurrentArticleIndex, setTopheadlines, setNumberOfArticles, numberOfArticles } = useNewsContext();

    const titleRef = createRef(null);
    const descriptionRef = createRef(null);
    const sourceRef = createRef(null);
    const authorRef = createRef(null);
    const dateRef = createRef(null);
    const anchorRef = createRef(null);
    const iconRef = createRef(null);

    useEffect(() => {
        setLoading(true)
        let activeCategory = "";
        categories.map((cat) => {
            if (cat.active === "true") {
                activeCategory = cat.name;
            }
            return activeCategory;
        })

        let url;
        if(activeCategory.length !== 0){
            url = `https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/NewsSearchAPI?q=${activeCategory}&pageNumber=1&pageSize=25&autoCorrect=true&fromPublishedDate=null&toPublishedDate=null`
        }else{
            url = `https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/TrendingNewsAPI?pageNumber=1&pageSize=25&withThumbnails=false`;
        }

        fetch(url, {
            "method": "GET",
            "headers": {
                "x-rapidapi-key": "9a3b47d10bmsh62eb82757b3fe3dp1b2469jsnbbfea26a725c",
                "x-rapidapi-host": "contextualwebsearch-websearch-v1.p.rapidapi.com"
            }
        })
        .then(async response => {
            const news = await response.json();
            setLoading(false);
            setNumberOfArticles(news.value.length);
            setCurrentArticleIndex(0);
            setTopheadlines(news.value);
            setFetchError("")
        })
        .catch(error => {
            console.log(error);
            setLoading(false);
            setFetchError(error)
        });
       
    }, [categories, setCurrentArticleIndex, setFetchError, setLoading, setNumberOfArticles, setTopheadlines]);


    useEffect(() => {
        if (isLoading) return
        if (!_.isEmpty(topHeadlines)) {
            try {
                const currentArticle = topHeadlines[currentArticleIndex];
                titleRef.current.innerText = currentArticle.title;
                descriptionRef.current.innerText = currentArticle.description;
                sourceRef.current.innerText = currentArticle.provider.name;
                dateRef.current.innerText = currentArticle.datePublished;
                anchorRef.current.href = currentArticle.url;
                titleRef.current.append(iconRef.current);
            } catch (error) {

            }
        }

    }, [topHeadlines, currentArticleIndex, isLoading, titleRef, descriptionRef, sourceRef, authorRef, dateRef, anchorRef, iconRef])

    if (numberOfArticles === 0) {
        return <NoResults />
    }

    return (
        <>
            {isLoading ? <Loader></Loader> :
                <>
                    <div>
                        <Indicator>{currentArticleIndex + 1}/{numberOfArticles}</Indicator>
                        <div >
                            <Anchor href='.' target="_blank" ref={anchorRef}>
                                <ArticleTitle ref={titleRef}>

                                </ArticleTitle>
                                <LaunchIcon ref={iconRef} />
                                <ArticleDescription ref={descriptionRef}>
                                </ArticleDescription>
                            </Anchor>
                        </div>
                    </div>
                    <Footer>
                        <div>
                            <Paragraph smalltext ref={sourceRef}></Paragraph>
                            <Paragraph smalltext ref={authorRef}></Paragraph>
                            <Paragraph smalltext ref={dateRef}></Paragraph>
                        </div>
                    </Footer>
                </>}

        </>
    )
}

export default Article;