import React, {useState, useEffect, useCallback} from 'react';
import { SearchWrapper, Input, SearchAnchor } from '../StyledComponents';
import SearchIcon from '@material-ui/icons/Search';
import { useNewsContext } from '../context';
const Search = () =>{

    const { theme, onSearchClick } = useNewsContext();
    const [keyword, setKeyword] = useState("");

    const onEnterKeyDown = useCallback(event => {
            if (event.keyCode === 13) {
                onSearchClick(keyword)
            }
    }, [keyword, onSearchClick])

    useEffect(() => {

        window.addEventListener('keydown', onEnterKeyDown)
        return () => window.removeEventListener('keydown', onEnterKeyDown)

    }, [onEnterKeyDown])

    return <SearchWrapper >
        <SearchIcon onClick={()=> onSearchClick(keyword)}/>
        <Input theme={theme} placeholder="Type to search for something"  value={keyword} onChange={(e) => setKeyword(e.target.value)}/>
        <SearchAnchor href="#" theme={theme}/>
    </SearchWrapper>
}

export default Search;