import React from 'react';
import { useNewsContext } from '../context';
import BrightnessHighIcon from '@material-ui/icons/BrightnessHigh';
import Brightness4Icon from '@material-ui/icons/Brightness4';
import { Thememode } from '../StyledComponents';

const ThemeMode = () =>{

    const {theme, setTheme} = useNewsContext();

    const onThemeChangeHandle = (e) => {
        e.stopPropagation();
        setTheme(theme.mode === "dark" ? { mode: "light" } : { mode: "dark" })
    }

    return (
        <Thememode>
            {theme.mode === "dark" ? <BrightnessHighIcon onClick={(e) => onThemeChangeHandle(e)} /> : <Brightness4Icon onClick={(e) => onThemeChangeHandle(e)} />}
        </Thememode>
        
    )
}

export default ThemeMode;